<template>
  <div class="container">
    <h1>Simon Says</h1>
    <div class="simon">
      <ul>
        <li
          @click="clickField(1), playSounds(1)"
          @mousedown="redIsGlowing = !redIsGlowing"
          @mouseup="redIsGlowing = !redIsGlowing"
          :class="{ glow: redIsGlowing }"
          class="red"
          ref="1"
        ></li>
        <li
          @click="clickField(2), playSounds(2)"
          @mousedown="blueIsGlowing = !blueIsGlowing"
          @mouseup="blueIsGlowing = !blueIsGlowing"
          :class="{ glow: blueIsGlowing }"
          class="blue"
          ref="2"
        ></li>
        <li
          @click="clickField(3), playSounds(3)"
          @mousedown="yellowIsGlowing = !yellowIsGlowing"
          @mouseup="yellowIsGlowing = !yellowIsGlowing"
          :class="{ glow: yellowIsGlowing }"
          class="yellow"
          ref="3"
        ></li>
        <li
          @click="clickField(4), playSounds(4)"
          @mousedown="greenIsGlowing = !greenIsGlowing"
          @mouseup="greenIsGlowing = !greenIsGlowing"
          :class="{ glow: greenIsGlowing }"
          class="green"
          ref="4"
        ></li>
      </ul>
    </div>
    <div class="game-info">
      <h2>
        Round: <span>{{ round }}</span>
      </h2>
      <button @click="startGame">Start</button>
    </div>
    <div class="game-options">
      <h2>Game Options: {{ checkedTime }}</h2>
      <input
        v-model="time"
        type="radio"
        name="mode"
        value="easy"
        ref="easyLvl"
      />Easy<br />
      <input
        v-model="time"
        type="radio"
        name="mode"
        value="normal"
      />Normal<br />
      <input v-model="time" type="radio" name="mode" value="hard" />Hard<br />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      sequence: [],
      colorsToClick: [],
      round: 0,
      isGameStarted: false,
      check: false,
      time: null,
      redIsGlowing: false,
      blueIsGlowing: false,
      greenIsGlowing: false,
      yellowIsGlowing: false,
    };
  },
  methods: {
    startGame() {
      if (this.isGameStarted) {
        return;
      }
      this.nextRound();
      this.isGameStarted = true;
    },
    clickField(ref) {
      if (this.isGameStarted) {
        let shiftedColor = this.colorsToClick.shift();
        let actualResponse = ref;
        this.check = shiftedColor === actualResponse;
        this.roundCheck();
      }
    },
    nextRound() {
      this.round++;
      this.sequence.push(Math.floor(Math.random() * (4 - 1 + 1) + 1));
      this.colorsToClick = [...this.sequence];
      this.glowSection();
    },
    roundCheck() {
      if (this.colorsToClick.length === 0 && this.check) {
        this.nextRound();
      } else if (!this.check) {
        (this.sequence = []), (this.colorsToClick = []);
        this.round = 0;
        this.isGameStarted = false;
        alert("you lost");
      }
    },
    glowSection() {
      let count = 0;
      let interval = setInterval(() => {
        this.turningOpacity(this.sequence[count]);
        this.playSounds(this.sequence[count]);
        count++;
        if (count >= this.sequence.length) {
          clearInterval(interval);
        }
      }, this.checkedTime);
    },
    turningOpacity(item) {
      this.$refs[item].classList.add("glow");
      setTimeout(() => {
        this.$refs[item].classList.remove("glow");
      }, 300);
    },
    playSounds(item) {
      if (this.isGameStarted) {
        let sound = new Audio(require(`@/assets/sounds/simonSound${item}.mp3`));
        sound.play();
      } else {
        return;
      }
    },
  },
  computed: {
    checkedTime() {
      switch (this.time) {
        case "hard":
          return 400;
        case "normal":
          return 1000;
        default:
          return 1500;
      }
    },
  },
  mounted() {
    this.$refs["easyLvl"].checked = true;
  },
};
</script>
  
<style>
.glow {
  opacity: 1 !important;
}

ul,
li {
  padding: 0;
  margin: 0;
}
.simon {
  background: #fff;
  position: relative;
  float: left;
  margin-right: 3em;
  width: 302px;
  height: 295px;
  -webkit-border-radius: 150px;
  border-radius: 150px;
  -moz-box-shadow: 2px 1px 12px #aaa;
  -webkit-box-shadow: 2px 1px 12px #aaa;
  -o-box-shadow: 2px 1px 12px #aaa;
  box-shadow: 2px 1px 12px #aaa;
}
.red,
.blue,
.yellow,
.green {
  opacity: 0.6;
  height: 290px;
  -webkit-border-radius: 150px;
  border-radius: 150px;
  position: absolute;
}
.red {
  background: #ff5643;
  clip: rect(0px, 300px, 150px, 150px);
  width: 296px;
}
.blue {
  background: dodgerblue;
  clip: rect(0px, 150px, 150px, 0px);
  width: 300px;
}
.yellow {
  background: #feef33;
  clip: rect(150px, 150px, 300px, 0px);
  width: 300px;
}
.green {
  background: #bede15;
  clip: rect(150px, 300px, 300px, 150px);
  width: 296px;
}
</style>
